import React from "react";
import moment from "moment";
import styled from "styled-components";
import * as Amp from "react-amphtml";

const AmpAuthor = styled.div`
  align-items: center;
  display: flex;
  margin-top: 20px;
  max-width: 500px;
`;

const AmpAuthorInfo = styled.div`
  margin-left: 20px;
`;

const AmpAuthorName = styled.div`
  font-size: 16px;
  margin-bottom: 4px;
`;

const AmpAuthorDate = styled.div`
  color: rgba(0, 0, 0, 0.54);
  font-size: 16px;
`;

const StyledAmpImage = styled(Amp.AmpImg)`
  border-radius: 30px;
  flex-shrink: 0;
`;

const AmpAuthorComponent = ({ date_added, image, name }) => (
  <AmpAuthor>
    <StyledAmpImage
      specName="default"
      src={image}
      width="60"
      height="60"
      alt="AMP"
    />
    {/* <amp-img src={image} height="60" width="60" /> */}
    <AmpAuthorInfo>
      <AmpAuthorName>{name}</AmpAuthorName>
      {date_added ? (
        <AmpAuthorDate>
          {moment(new Date(date_added)).format("MMM Do, YYYY")}
        </AmpAuthorDate>
      ) : (
        <AmpAuthorDate>Not published yet</AmpAuthorDate>
      )}
    </AmpAuthorInfo>
  </AmpAuthor>
);

export { AmpAuthorComponent };
